import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
import page from '@/components/common-components/page.vue';
import imageUpload from '@/components/common-components/image-upload.vue';
import { deepClone } from '@/until/index';
import { EventBus } from '@/until/event-bus';
let AppList = class AppList extends Vue {
    constructor() {
        super(...arguments);
        this.title = '店铺新增';
        this.model = {
            name: '',
            transType: ['sf'],
            imgUrlList: [],
            address: '',
            phone: ''
        };
        this.fields = [
            {
                type: 'input',
                modelKey: 'name',
                label: '商店名称',
                props: {
                    placeholder: '请输入'
                },
                rules: {
                    required: true
                },
                // validating when blur
                trigger: 'blur'
            },
            {
                type: 'checkbox-group',
                modelKey: 'transType',
                label: '配送方式',
                props: {
                    options: [{
                            label: '客户自提',
                            value: 'sf'
                        }, {
                            label: '配送',
                            value: 'ds'
                        }]
                },
                rules: {
                    required: true
                }
            },
            {
                type: 'input',
                modelKey: 'address',
                label: '店铺地址',
                props: {
                    placeholder: '请输入'
                },
                rules: {
                    required: true
                },
                // validating when blur
                trigger: 'blur'
            },
            {
                type: 'input',
                modelKey: 'phone',
                label: '商家电话',
                props: {
                    placeholder: '请输入'
                },
                rules: {
                    required: true
                },
                // validating when blur
                trigger: 'blur'
            },
        ];
        this.options = {
            scrollToInvalidField: true,
            layout: 'standard' // classic fresh
        };
    }
    validateHandler() {
        console.log('validateHandler');
    }
    submitHandler(e) {
        e.preventDefault();
        let businessId = this.$store.state.userInfo.business;
        let queryObj;
        queryObj = { ...this.model, businessId: businessId };
        this.$store.dispatch('addOrUpdateShop', queryObj).then((response) => {
            if (response.success) {
                if (response.data) {
                    EventBus.$emit("shopList", { type: 'add', data: response.data });
                }
                else {
                    EventBus.$emit('shopList', { type: 'update', data: queryObj });
                }
                this.$router.back();
                const toast = this.$createToast({
                    txt: '保存成功',
                    time: 3000,
                    type: 'txt'
                });
                toast.show();
            }
        });
    }
    resetHandler() {
        this.$emit('cancel');
    }
    mounted() {
        let shopData = this.$route.params;
        if (shopData.id) {
            this.title = '店铺修改';
            if (!shopData.transType) {
                shopData.transType = ['sf'];
            }
            let cloneData = {
                id: shopData.id,
                name: shopData.name,
                transType: shopData.transType,
                businessId: shopData.businessId,
                address: shopData.address,
                phone: shopData.phone
            };
            this.$set(this, 'model', deepClone(shopData));
        }
    }
    submitClick() {
        this.$refs.cubeForm.submit();
    }
};
AppList = __decorate([
    Component({
        components: {
            page,
            imageUpload
        }
    })
], AppList);
export default AppList;
